import React, { useState } from 'react';
import { Dialog, DialogContent, DialogTitle, IconButton } from '@mui/material';
import { XMarkIcon } from '@heroicons/react/24/outline'; // 使用 HeroIcons 的叉叉圖示
import { colors } from "../../../styles/colors";
import { showCustomToast } from '../../CustomToast';
import { useTranslation } from 'react-i18next';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { RootState } from '../../../store';

interface ModalProps {
  open: boolean;
  title?: string;
  content?: string[];
  onClose: () => void;
  onClick: (category: number) => void;
  datasetId?:string;
  type?: string;
  defaultIndex?: number; 
  selectedIndex?: number
}

const ListModal: React.FC<ModalProps> = ({
  open,
  onClose,
  onClick,
  title = '',
  content,
  type,
  defaultIndex = -1,
  selectedIndex = -1
}) => {

  const [selectedIndices, setSelectedIndices] = useState<number>(-1);
  const {t} = useTranslation();
  const player = useSelector((state: RootState) => state.init.player);
  const stream = useSelector((state: RootState) => state.init.stream);
  const favor = useSelector((state: RootState) => state.init.favor);

  useEffect(() => {
    setSelectedIndices(selectedIndex);
  }, [selectedIndex]);

  useEffect(() => {
    setSelectedIndices(defaultIndex);
  }, [defaultIndex]);

  const toggleHighlight = (index: number, category: string) => {

    onClick(index);
   
    setSelectedIndices(index);
    if (type === 'favor') {
      showCustomToast(t("新增最愛成功"), "success");
    } else if (type !== "stream"){
      onClose();
    }
    else {
      //showCustomToast(t("切換線路成功"), "success");
    }
  };
  useEffect(() => {
    switch (type) {
      case "player": 
        setSelectedIndices(content?.indexOf(player) ?? 0);
        break;
      case "stream": 
        setSelectedIndices(content?.indexOf(stream) ?? 1);
        break;
    }
  }, [type])

  return (
    <Dialog open={open} onClose={onClose} fullWidth maxWidth="sm">
      <div className='w-full' style={{backgroundColor: colors.background}}>
        <div className="flex justify-center items-start relative">
          <DialogTitle className="text-white">{title}</DialogTitle>
          <IconButton onClick={onClose} style={{position: 'absolute', right:'2px'}}>
            <XMarkIcon className="h-6 w-6 text-white" />
          </IconButton>
        </div>
        <DialogContent>
          {content?.map((line, index: number) => (
            <p
              key={index}
              onClick={() => toggleHighlight(index, line)}
              className={`text-center cursor-pointer my-2 ${index === selectedIndices ? 'text-red-500' : 'text-white'}`}
            >
              {t(line)}
            </p>
          ))}
        </DialogContent>
      </div> 
    </Dialog>
  );
};

export default ListModal;
