import { useState, useMemo, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faEye,
  faHeart,
  faDownload,
  faArrowRightArrowLeft,
  faPlay
} from "@fortawesome/free-solid-svg-icons";
import { faHeart as faRegHeart } from "@fortawesome/free-regular-svg-icons";
import { ChevronDownIcon, ChevronUpIcon } from "@heroicons/react/24/solid";

import Tags from "../Tags";
import ButtonGroup from "../ButtonGroup";
import Modal from "../Modal";
import PlanSelection from "../Modal/PlanSelection";
import Button from "../Button";
import DownloadModal from "../Modal/DownloadModal";
import { showCustomToast } from "../CustomToast";
import VolumeList from "../../components/VolumeList";

import { useSelector, useDispatch } from "react-redux";
import { RootState, AppDispatch } from "../../store";
import { favorite } from "../../store/favoritesSlice";
//import { fetchFavorites, removeFavoriteItem, addFavoriteItem, clearFavorites, favorite } from '../../store/favoritesSlice';
import {
  setComicInfo,
  fetchComicInfo,
  pointToComic,
  setComicPurchaseInfo,
  downloadComic,
  setCurrentChapter,
  setIsDownload,
} from "../../store/comicSlice";
import {
  setVideoInfo,
  fetchVideoInfo,
  pointToMv,
  setMvPurchaseInfo,
  downloadMv,
  setVurlDomain,
  setVideoUrl,
} from "../../store/videoSlice";

import { FavoriteItem } from "../../utils/pouchdb";

import {
  Factory,
  ProductDetails,
  PurchaseInfo,
  Tag,
} from "../../type/videoType";
import { Tag as comicTag } from "../../type/comicType";

import useUserInfo from "../../utils/checkUserInfoUtils";
import ListModal from "../Modal/ListModal";

import { colors } from "../../styles/colors";
import { Details } from "@mui/icons-material";
import { Favorite, PostParam } from "../../type/favorite";
import { fetchUserInfo } from "../../store/userInfoSlice";
import { detectChannel, isExclusiveVideo, getFontClass, switchName, testSpeed, isIOS, unixtime2Date, unixtime2Datetime } from "../../utils/helper";
import Loading from "../Loading";
import { CircularProgress, Box } from "@mui/material";
import { setFavor, setPlayer, setStream } from "../../store/initSlice";

interface DescriptionProps {
  type: "video" | "comic";
  setTabIndex?: (index: number) => void;
}

const Description: React.FC<DescriptionProps> = ({ type, setTabIndex }) => {
  //const { userInfo, checkUserOwnProduct } = useUserInfo();
  const currentChapter = useSelector(
    (state: RootState) => state.comic.currentChapter
  );
  const userInfo = useSelector(
    (state: RootState) => state.userInfo.userInfoData?.data
  );
  const videoInfo = useSelector(
    (state: RootState) => state.video.videoInfoData
  );
  const comicInfo = useSelector(
    (state: RootState) => state.comic.comicInfoData
  );
  const folderList = useSelector(
    (state: RootState) => state.favorites.folderList?.data
  );
  const language = useSelector((state: RootState) => state.init.language);

  const [uuid, setUuid] = useState<string>(
    type === "video"
      ? videoInfo?.data?.uuid ?? ""
      : comicInfo?.comic_info?.ci_uuid ?? ""
  );
  const [isFavor, setIsFavor] = useState(false);
  const [hit, setHit] = useState("0");
  const [name, setName] = useState("");
  const [des, setDes] = useState("");
  const [barcode, setBarcode] = useState("");
  const [tags, setTags] = useState<Tag[] | comicTag[]>([]);
  const [factories, setFactories] = useState<string[]>([]);
  const [isDescriptionOpened, setIsDescriptionOpened] = useState(false);
  const [favorIsOpen, setFavorIsOpen] = useState(false);
  const [switchStreamIsOpen, setSwitchStreamIsOpen] = useState(false);
  const [playerIsOpen, setPlayerIsOpen] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const [modalContent, setModalContent] = useState("");
  const [isPlanSelectionOpen, setIsPlanSelectionOpen] = useState(false);
  const [downloadOpen, setDownloadOpen] = useState(false);
  const [isPickEpisodeGuideOpen, setIsPickEpisodeGuideOpen] = useState(false);
  const [channelsSpeed, setChannelsSpeed] = useState<Record<number, string>[]>([]);

  const navigate = useNavigate();
  const { t } = useTranslation();
  const dispatch: AppDispatch = useDispatch();

  const [selectedStream, setSelectedStream] = useState<number>(-1); //當前選中的線路
  const [isDetecting, setIsDetecting] = useState<boolean>(false); //偵測狀態
  const [streamContent, setStreamContent] = useState<string[]>([
    "偵測",
    "KO",
    "KF",
  ]); // 彈窗內容
  const [selectedPlayer, setSelectedPlayer] = useState<number>(-1); //當前選中的線路
  const [playerContent, setPlayerContent] = useState<string[]>([
    "JW",
    "VideoJS",
    "Plyr",
  ]); // 彈窗內容
  const [progress, setProgress] = useState(0);
  const [btnGroupIndex, setBtnGroupIndex] = useState<number>(-1);

  useEffect(() => {
    if (videoInfo || comicInfo) {
      setUuid(
        type === "video"
          ? videoInfo?.data?.uuid ?? ""
          : comicInfo?.comic_info?.ci_uuid ?? ""
      );
    }
  }, [videoInfo, comicInfo]);

  useEffect(() => {
    if (userInfo) {
      if ((favoritesByUuid() ?? []).length > 0) {
        setIsFavor(true);
      } else {
        setIsFavor(false);
      }
    }
  }, [userInfo?.my_favorite_list, uuid]);

  useEffect(() => {
    if (videoInfo) {
      setBarcode(videoInfo?.data?.barcode);
      setHit(videoInfo?.data?.hits ?? "0");
      setName(switchName(videoInfo?.data, language));
      setDes(switchName( videoInfo?.data?.Des, language));
      if (typeof videoInfo?.data?.tag_ids === 'object') {
        setTags(Object.values(videoInfo?.data?.tag_ids) ?? []);
      }
      let tmpFactories: string[] = [];
      videoInfo?.data?.factories_ids.forEach((item:any) => {
        tmpFactories.push(item?.name);
      });
      setFactories(tmpFactories);
    }
  }, [videoInfo]);

  useEffect(() => {
    if (comicInfo && type === "comic") {
      setBarcode("");
      setHit(comicInfo?.comic_info?.hit_in ?? "0");
      setName(comicInfo?.comic_info?.ci_name ?? "");
      setDes("");
      setTags(typeof comicInfo?.comic_info?.tags === "object" && comicInfo?.comic_info?.tags !== null 
        ? Object.values(comicInfo?.comic_info?.tags)
        :  []
      );
      setFactories([comicInfo?.comic_info?.ci_author]);
    }
  }, [comicInfo]);

  useEffect(() => {
    dispatch(favorite({ method: "getAllFolder" }));
  }, [dispatch, userInfo, uuid]);

  useEffect(() => {
    // 清理函數，在組件卸載或依賴變化時執行
    return () => {
      dispatch(setCurrentChapter(null));
    };
  }, [dispatch]);

  const handleFavorOpen = () => {
    if (userInfo && folderList) {
      if (folderList.length > 0 && !isFavor) {
        setFavorIsOpen(true);
      } else if (isFavor) {
        const data = JSON.stringify([uuid]);
        const config: PostParam = {
          data: data,
        };
        dispatch(favorite({ method: "delVideo", post_param: config }))
          .then((result) => {
            const payload = result?.payload as Favorite;
            if (payload?.code === -1) {
              dispatch(fetchUserInfo());
              showCustomToast(t("移除最愛成功"), "error");
            } else {
              console.log("delVideo exception: ", payload);
            }
          })
          .catch((error) => {
            showCustomToast(error, "error");
          });
      }
    } else {
      navigate("/app/login");
    }
  };

  const handleAddFavorite = (index: number) => {
    const favorContent = [...new Set(folderList?.map((item) => item?.folder_name))];
    dispatch(setFavor(favorContent[index]));
    if (folderList) {
      const config: PostParam = {
        folder_id: folderList[index]?.folder_id,
        video_id: uuid,
      };
      dispatch(favorite({ method: "addFavo", post_param: config }))
        .then((result) => {
          const payload = result?.payload as Favorite;
          if (payload?.code === -1) {
            dispatch(fetchUserInfo());
            showCustomToast(t("新增最愛成功"), "error");
          } else {
            console.log("addFavo exception: ", payload);
          }
        })
        .catch((error) => {
          showCustomToast(error, "error");
        });
    }
  };

  const handleFavorClose = () => {
    setFavorIsOpen(false);
    setBtnGroupIndex(-1);
  };
  const handleSwitchStreamOpen = () => setSwitchStreamIsOpen(true);
  const handleSwitchStreamClose = () => {
    setSwitchStreamIsOpen(false);
    setBtnGroupIndex(-1);
  };
  const handleClose = () => setIsOpen(false);
  const handleConfirm = () => {
    navigate("/app/Recharge");
  };
  const handleCancel = () => {
    setIsOpen(false);
  };
  const handleDownloadClose = () => {
    setDownloadOpen(false);
    setBtnGroupIndex(-1);
  };

  const handlePlayerClose = () => {
    setPlayerIsOpen(false);
    setBtnGroupIndex(-1);
  };

  const handlePlayerOpen = () => {
    setPlayerIsOpen(true);
  };

  const handlePlanSelectionOpen = () => {
    if (userInfo && typeof userInfo === "object") {
      const msId = videoInfo?.data?.ms_ids
        .split(",")
        .filter((item) => item !== "");
      const config = {
        num: uuid,
        ms_id: msId?.[0] ?? "o6",
        file_prefix: "",
        cat_id: "15",
        quality: "2",
      };
      if (type === "video") {
        dispatch(downloadMv(config))
          .then((result) => {
            console.log(result);
            if (result.payload?.error) {
              if (type === "video") {
                if (
                  videoInfo &&
                  videoInfo?.data &&
                  Object.keys(videoInfo?.data)?.length > 0
                ) {
                  if (
                    userInfo?.point >= Number(videoInfo?.data?.point) ||
                    Number(userInfo?.ex_tickets?.video_download) > 0
                  ) {
                    dispatch(
                      pointToMv({
                        num: uuid,
                        dw: "1",
                      })
                    )
                      .then((result) => {
                        if (result?.payload?.result === "ok") {
                          showCustomToast(result?.payload?.result_msg, "error");
                        } else if (!result?.payload?.error) {
                          dispatch(
                            setMvPurchaseInfo(result?.payload as PurchaseInfo)
                          );
                          setIsPlanSelectionOpen(true);
                        }
                      })
                      .catch((error) => {
                        console.error("Fetch failed:", error);
                      });
                  } else {
                    setModalContent(t("點數、下載券不足，是否前往加值?"));
                    setIsOpen(true);
                  }
                }
              }
            } else {
              setDownloadOpen(true);
            }
          })
          .catch((error) => {
            console.log("downloadMv exception: ", error);
          });
      } else if (type === "comic") {
        if (setTabIndex) {
          setTabIndex(1);
        }
        dispatch(setIsDownload(true));
      }
    } else {
      navigate("/app/login");
    }
  };

  const handlePlayerSelect = (index: number) => {
    console.log("index: ", index);
    console.log("playerContent: ", playerContent[index]);
    setSelectedPlayer(index);
    dispatch(setPlayer(playerContent[index]));
    dispatch(setVideoUrl(""));
  }

  const handle2Search = (kw: string) => {
    navigate("/app/search", {
      state: {
        kw: kw,
        cate: type === "video" ? "" : "amine",
      },
    });
  };

  const buttonGroup = useMemo(() => {
    const buttons = [
      //{ label: t("最愛"), icon: isFavor ? faHeart : faRegHeart },
      { label: t("下載"), icon: faDownload }
    ];

    // 當 type 是 video 時，添加 "切換線路" 按鈕
    if (type === "video") {
      buttons.push({ label: t("線路"), icon: faArrowRightArrowLeft });
      buttons.push({ label: t("播放器"), icon: faPlay });
    }

    return buttons;
  }, [t, isFavor, type, userInfo]);

  const handleButtons = [
    <Button
      className="button-full active mt-1 w-1/3"
      type="button"
      onClick={handleConfirm}
    >
      {t("確認")}
    </Button>,
    <Button
      className="button-full active mt-1 w-1/3"
      type="button"
      onClick={handleCancel}
    >
      {t("取消")}
    </Button>,
  ];

  const favoritesByUuid = () => {
    if (
      userInfo &&
      userInfo?.my_favorite_list &&
      userInfo?.my_favorite_list?.length !== 0
    ) {
      const favoriteItems = userInfo?.my_favorite_list?.filter(
        (item) => item === uuid
      );
      return favoriteItems;
    } else {
      return null;
    }
  };

  //點擊偵測事件
  const handleStreamSelect = (index: number) => {
    const selected = streamContent[index];
    dispatch(setStream(selected));

    if (selected === "偵測") {
      handleStreamDetect(); // 點擊偵測時執行偵測邏輯
      
    } else {
      setSelectedStream(index);
      const selectedServer = videoInfo?.data?.PV_ms_channels?.[selected]?.servers;
      if (typeof selectedServer === "object" && selectedServer !== null) {
        const values = Object.values(selectedServer);
        dispatch(setVurlDomain(values[0] as string));
      } else {
        dispatch(setVurlDomain(""));
      }
      dispatch(setVideoUrl(""));
    }
  };

  useEffect(() => {
    console.log("channelsSpeed: ", channelsSpeed);
    console.log("selectedStream: ", selectedStream);
  }, [channelsSpeed, selectedStream]);

  //模擬偵測進度，並更新線路
  const handleStreamDetect = async () => {
    const channelList = detectChannel(videoInfo);
    const tempSpeedList: Record<number, string>[] = [];
  
    let fastChannel = "";
    let fastUrl = "";
    let minimumSpeed = 2147483647;

    if (!channelList) return;

    setIsDetecting(true);
    setProgress(0);
  
    for (const obj of channelList) {
      const url = `${obj?.value}/ms/speed_file.php`;
      const speed = await testSpeed(url);
  
      if (speed !== undefined) {
  
        if (speed < minimumSpeed) {
          minimumSpeed = speed;
          fastChannel = obj?.key;
          fastUrl = obj?.value;
        }
      } else {
        console.warn("測速失敗：", fastUrl);
      }
    }

    dispatch(setVurlDomain(fastUrl));
    dispatch(setVideoUrl(""));
    setSelectedStream(streamContent.indexOf(fastChannel));
  
    const interval = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 100) {
          clearInterval(interval);
          setIsDetecting(false);
          return 100;
        }
        return prev + 20;
      });
    }, 200);
  };
  

  return (
    <div className={`${getFontClass(`${language}_m`)} p-4 space-y-2`}>
      <div className="flex space-x-3 items-center justify-start overflow-x-auto hidden-scrollbar">
        <div className="flex items-center justify-start">
          <FontAwesomeIcon icon={faEye} className="text-xs" />
          <span className="ml-1 text-xs">{hit}</span>
        </div>
        <Tags type={type} tags={tags || []} />
      </div>
      {barcode !== "" && <span className="text-l font-bold">{barcode}</span>}
      <p className="text-l font-bold">{name}</p>
      <div
        className={`flex flex-wrap gap-x-3 pb-2 ${
          type === "video" && "border-slate-600 border-b-2"
        }`}
      >
        {factories?.map((item, key) => (
          <span
            key={key}
            className="bg-slate-400 rounded-md px-2 text-black"
            onClick={() => handle2Search(item)}
          >
            {item}
          </span>
        ))}
        <span>{unixtime2Date(Number(videoInfo?.data?.timestamp_publish))}</span>
      </div>

      {type === "video" && (
        <div className="flex gap-x-3 items-center">
          <span className="shrink-0">{t("系列作：")}</span>
          {videoInfo?.data?.series_ids?.map((item) => (
            <span
              className=" rounded-md p-1 text-white border text-xs flex items-center"
              onClick={() => handle2Search(item?.name)}
            >
              {switchName(item, language)}
            </span>
          ))}
        </div>
      )}

      <div className="flex flex-col items-end space-y-2">
        <div className="flex items-start w-full">
          <p
            className={`text-sm text-gray-400 ${
              isDescriptionOpened ? "" : "line-clamp-2"
            }`}
          >
            {type === "video" && isExclusiveVideo(videoInfo) && (
              <span className="mr-2 px-2 text-gray-800" style={{backgroundColor: "rgb(95 215 0)"}}>{t("獨家")}</span>
            )}
            {des}
          </p>
        </div>
        {type === "video" && (
          <div
            className="px-1"
            onClick={() => setIsDescriptionOpened(!isDescriptionOpened)}
            style={{ marginTop: "0px" }}
          >
            {isDescriptionOpened ? (
              <ChevronUpIcon className="h-6 w-6 py-0 mt-[-0.4rem]" />
            ) : (
              <ChevronDownIcon className="h-6 w-6 py-0 mt-[-0.4rem]" />
            )}
          </div>
        )}
      </div>
      <Button  
        className={`active button-full ${isFavor ? "bg-red-500 text-white" : "bg-red-700 text-gray-100"}`}
        onClick={handleFavorOpen}
      >
        <FontAwesomeIcon icon={isFavor ? faHeart : faRegHeart} className="mr-2" />
        {t("加入最愛")}
      </Button>
      <ButtonGroup
        buttons={buttonGroup}
        handleClicks={
          [handlePlanSelectionOpen, handleSwitchStreamOpen, handlePlayerOpen]
        }
        setBtnGroupIndex={setBtnGroupIndex}
        btnGroupIndex={btnGroupIndex}
      />
      <ListModal
        title={t("最愛")}
        open={favorIsOpen}
        onClose={handleFavorClose}
        content={[...new Set(folderList?.map((item) => item?.folder_name))]}
        onClick={handleAddFavorite}
        type="favor"
      />
      <ListModal
        title={t("線路")}
        open={switchStreamIsOpen}
        onClose={handleSwitchStreamClose}
        content={streamContent}
        type="stream"
        onClick={(index) => handleStreamSelect(index)}
        defaultIndex={selectedStream}
      />
      <ListModal
        title={t("播放器")}
        open={playerIsOpen}
        onClose={handlePlayerClose}
        content={playerContent}
        type="player"
        onClick={(index) => handlePlayerSelect(index)}
        defaultIndex={selectedPlayer ?? 0}
      />
      {isDetecting && (
        <div
          className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-70 h-screen"
          style={{ zIndex: 1400, marginTop: 0 }}
        >
          <Box position="relative" display="inline-flex">
            <CircularProgress variant="determinate" value={progress} />
            <Box
              top={0}
              left={0}
              bottom={0}
              right={0}
              position="absolute"
              display="flex"
              alignItems="center"
              justifyContent="center"
            >
              <span className="text-white font-bold">{`${progress}%`}</span>
            </Box>
          </Box>
        </div>
      )}
      {((videoInfo && videoInfo?.data?.point) ||
        (comicInfo && comicInfo?.comic_info?.ci_point)) && (
        <PlanSelection
          open={isPlanSelectionOpen}
          setIsPlanSelectionOpen={setIsPlanSelectionOpen}
          costPoint={
            type === "video"
              ? Number(videoInfo?.data?.point)
              : Number(comicInfo?.comic_info?.ci_point)
          }
          type={`${type}DownloadTicket`}
          setBtnGroupIndex={setBtnGroupIndex}
        />
      )}
      <Modal
        open={isOpen}
        onClose={handleClose}
        content={[modalContent]}
        buttons={handleButtons}
      />
      <DownloadModal
        open={downloadOpen}
        onClose={handleDownloadClose}
        title={t("請選擇您要下載的影片畫質")}
        type={type}
      />
    </div>
  );
};
export default Description;
