import {useEffect, useState} from "react";
import { Dialog, DialogContent, DialogActions, IconButton, TextField, Button } from "@mui/material";
import { XMarkIcon, TicketIcon } from "@heroicons/react/24/outline";

import { showCustomToast } from "../../../components/CustomToast";
import Modal from "../../Modal";
import { colors } from "../../../styles/colors";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "../../../store";
import { useTranslation } from "react-i18next";
import { setDownloading } from "../../../store/initSlice";

interface DownloadModalProps {
  open: boolean;
  onClose: () => void;
  title: string;
  type: "video" | "comic"
}

const DownloadModal: React.FC<DownloadModalProps> = ({ open, onClose, title, type }) => {
  const {t} = useTranslation();
  const dispatch:AppDispatch = useDispatch();
  const mvDownloadInfo = useSelector((state:RootState) => state.video.mvDownloadInfo);
  const comicDownloadInfo = useSelector((state:RootState) => state.comic.downloadInfo);
  const videoDownloadBtn = (
    (mvDownloadInfo?.filelinks ?? []).map((item, index) => (
      <a
        key={index}
        download
        href={item.vurl} // 下载链接
        rel="noopener noreferrer" // 安全性
        className="flex flex-col items-center justify-center font-bold text-gray-100 rounded-md"
        style={{ 
          backgroundColor: 'red',
          width: '100%',
          height: '100%',
          textDecoration: 'none' // 去掉默认的链接样式
        }}
        onClick={()=> {
          dispatch(setDownloading(true));
          alert(t("已於背景開始下載"));
        }}
      >
        <span className="text-xl">{`${item.type}${t('畫質')}`}</span>
        <span className="text-xs">{item.size}</span>
      </a>
    ))
  );

  const comicDownloadBtn = (
    <div className="flex items-start justify-center space-x-4 w-full h-full py-2">
      <a 
        href={`https:${comicDownloadInfo?.filelinkPDF}`} 
        download 
        target="_blank"
        className="flex flex-col items-center justify-center font-bold text-gray-100 rounded-md" 
        style={{ 
          backgroundColor: 'red', 
          width: '50%',
          height: '100%',
          textDecoration: 'none'
        }}
      >
        <span className="text-xl">pdf</span>
      </a>
      <a 
        href={`https:${comicDownloadInfo?.filelinkZIP}`} 
        download 
        target="_blank"
        className="flex flex-col items-center justify-center font-bold text-gray-100 rounded-md" 
        style={{ 
          backgroundColor: 'red', 
          width: '50%',
          height: '100%',
          textDecoration: 'none'
        }}
      >
        <span className="text-xl">zip</span>
      </a>
    </div>
  );

  const videoFooterInfo = (
    <div className="w-full flex flex-col text-xs text-gray-200">
      <span>{t('*提醒您：')}</span>
      <span>{t('1. 建議使用Wi-fi電腦或下載，避免產生額外網路費用')}</span>
      <span>{t('2. 可能需要下載解壓縮或PDF閱讀軟體開啟檔案')}</span>
      <span className="text-red-500">{t('*使用電腦版下載pdf請滑鼠右鍵[另存新檔]*')}</span>
    </div>
  );

  const comicFooterInfo = (
    <div className="w-full flex flex-col text-xs text-gray-200">
      <span>{t('*提醒您：')}</span>
      <span>{t('1. 建議使用Wi-fi電腦或下載，避免產生額外網路費用')}</span>
      <span>2. <span className="text-red-500">{t('電腦')}</span>{t('下載時請於需下載的畫質選項上按右鍵')}<span className="text-red-500">{t('另存新檔')}</span>{t('另存新檔')}</span>
    </div>
  );

  return (
    <Dialog 
      open={open} 
      onClose={onClose} 
      PaperProps={{
        style: {
          width: "100%", // 寬度設置為 90%          
          height: "28%", // 高度設置為 80%
        },
      }}
    >
      <div className="bg-gray-800 p-3 rounded-t-lg w-full h-full">
        {/* Header */}
        <div className="flex justify-center items-center">
          <h2 className="text-white text-lg font-bold relative">{title}</h2>
          <XMarkIcon onClick={onClose} className="h-5 w-5 text-white absolute right-1"/>
        </div>

        <DialogContent className="bg-gray-800 w-full h-2/4" style={{ padding: '10px 0px' }} >
          {/* Input Field */}
          {type === 'video' ? videoDownloadBtn : comicDownloadBtn}
        </DialogContent>

        <DialogActions className="flex bg-gray-800 w-full justify-start p-0">
         {type === 'video' ? videoFooterInfo : comicFooterInfo}
        </DialogActions>
      </div>
    </Dialog>
  );
};

export default DownloadModal;
