import React, { useEffect, useState, useMemo, useCallback, useDebugValue } from 'react';

import Modal from '..';
import Button from "../../Button";
import UsageHandler from '../UsageHandle';

import { useDispatch, useSelector, UseSelector } from 'react-redux';
import { AppDispatch, RootState } from '../../../store';
import { pointToComic, setComicPurchase, setComicPurchaseInfo } from '../../../store/comicSlice';
import { showCustomToast } from '../../CustomToast';
import { PurchaseInfo } from '../../../type/comicType';
import { useTranslation } from 'react-i18next';

interface PlanSelectionProps {
  open: boolean;
  setIsPlanSelectionOpen: React.Dispatch<React.SetStateAction<boolean>>;
  costPoint: number;
  type?: "videoOnlineTicket" | "videoDownloadTicket" | "comicOnlineTicket" | "comicDownloadTicket";
  setBtnGroupIndex?: (index: number) => void;
};
interface ButtonConfig {
  buttonText: string;
  description: string;
  pointDesc: string;
  ref: 'video_online' | 'video_download' | 'comic_online' | 'comic_download';
}

const PlanSelection: React.FC<PlanSelectionProps> = ({ 
  open, 
  setIsPlanSelectionOpen, 
  costPoint, 
  type = "comicOnlineTicket",
  setBtnGroupIndex,
}) => {
  const {t} = useTranslation();
  const comicPurchaseInfo = useSelector((state: RootState) => state.comic.purchaseInfo);
  const mvPurchaseInfo = useSelector((state: RootState) => state.video.purchaseInfo);
  useEffect(() => {

  }, [mvPurchaseInfo]);
  // 配置按鈕文字和描述內容
  const buttonConfig: { [key: string]: ButtonConfig } = {
    'videoOnlineTicket': {
      buttonText: t("影片線上券*1"),
      description: t("確定使用影片線上券*1張，購買單片48小時無限次觀看?"),
      pointDesc: `${t('確定消費')}${costPoint}${t('點，購買單片48小時無限次觀看?')}`,
      ref: 'video_online'
    },
    'videoDownloadTicket': {
      buttonText: t("影片下載券*1"),
      description: t("確定使用影片下載券*1張，購買單片48小時無限次下載?"),
      pointDesc: `${t('確定消費')}${costPoint}${t('點，購買單片48小時無限次下載?')}`,
      ref: 'video_download'
    },
    'comicOnlineTicket': {
      buttonText: t("漫畫線上券*1"),
      description: t("確定使用漫畫券*1張，購買單話48小時無限次觀看?"),
      pointDesc: `${t('確定消費')}${comicPurchaseInfo?.needPoint}${t('點，購買整本48小時無限次觀看?')}`,
      ref: 'comic_online'
    },
    'comicDownloadTicket': {
      buttonText: t("漫畫下載券*1"),
      description: t("確定使用漫畫券*1張，購買單話48小時無限次下載?"),
      pointDesc: `${t('確定消費')}${comicPurchaseInfo?.needPoint}${t('點，購買整本48小時無限次下載?')}`,
      ref: 'comic_download'
    },
  };
  const currentConfig = buttonConfig[type];
  const [isUsageOpen, setIsUsageOpen] = useState(false);
  const [usePoint, setUsePoint] = useState(false);

  // 使用 useMemo 來產生描述文字和按鈕組件
  const description = useMemo(() => (
    usePoint ? currentConfig.pointDesc : currentConfig.description
  ), [usePoint, currentConfig]);

  const buttons = useMemo(() => [
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={() => handleUsageOpen(type)}>
      {currentConfig.buttonText}
    </Button>,
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={() => handleUsageOpen('point')}>
      {type.includes('comic') ? `${t('點數')}${comicPurchaseInfo?.needPoint}${t('點')}` : `${t('點數')}${costPoint}${t('點')}`}
    </Button>
  ], [type, comicPurchaseInfo, mvPurchaseInfo]);

  // 使用 useCallback 確保 handleUsageOpen 在依賴未改變時不重新建立
  const handleUsageOpen = useCallback((selectedType: string) => {
    if (selectedType === 'point') {
      //alert('功能施作中！');
      setUsePoint(selectedType === 'point');
      setIsUsageOpen(true);
    } else {
      setIsUsageOpen(true);
    }
    handleClose();
  }, []);

  const handleClose = useCallback(() => {
    setIsPlanSelectionOpen(false);
    if (setBtnGroupIndex) {
      setBtnGroupIndex(-1);
    }
  }, [setIsPlanSelectionOpen, setBtnGroupIndex]);

  return (
    <div>
      <Modal
        title={t("請選擇方案")}
        open={open}
        onClose={handleClose}
        buttons={buttons}
      />
      <UsageHandler
        open={isUsageOpen}
        onClose={() => {
          setIsUsageOpen(false);
          if (setBtnGroupIndex) {
            setBtnGroupIndex(-1);
          }
        }}
        usageType={type}
        usePoint={usePoint}
        cost={usePoint ? costPoint : 1}
        description={description}
      />
    </div>
  );
};

export default PlanSelection;
