import React, { useEffect, useState } from 'react';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Button from "../../components/Button";

interface ButtonItem {
  label: string;
  icon: any; // Define icon type more specifically if possible
}

interface ButtonGroupProps {
  buttons: ButtonItem[];
  handleClicks: (() => void)[];
  btnGroupIndex?: number;
  setBtnGroupIndex?: (index: number) => void;
}

function ButtonGroup({ buttons, handleClicks, btnGroupIndex, setBtnGroupIndex }: ButtonGroupProps) {
  const [clickedIndices, setClickedIndices] = useState<number>(-1);
  const handleButtonClick = (index: number) => {
    // 如果按鈕還未被點擊，加入到 clickedIndices 中
    if (setBtnGroupIndex) {
      setBtnGroupIndex(index);
    }
    setClickedIndices(index);
    // 執行對應的點擊處理函數
    handleClicks[index]();
  };

  useEffect(() => {
    if (setBtnGroupIndex && btnGroupIndex === -1) {
      setClickedIndices(-1);
    }
  }, [btnGroupIndex])

  return (
    <div className="flex justify-center space-x-2">
      {buttons.map((item, index) => (
        <Button
        key={index}
        className={`button-rounded secondary ${
          clickedIndices === index ? 'bg-red-500 text-white' : 'border border-red-500'
        }`}
        onClick={() => handleButtonClick(index)}
      >
        <FontAwesomeIcon icon={item.icon} className="mr-2" />
        {item.label}
      </Button>
      ))}
    </div>
  );
}

export default ButtonGroup;
