import React, { ReactNode, useEffect, useState } from 'react';
import Modal from '..';
import Button from "../../Button";
import { showCustomToast } from "../../CustomToast";

import { useSelector, useDispatch } from 'react-redux';  
import { AppDispatch, RootState } from '../../../store';
import * as wallet from '../../../store/walletSlice';
import * as video from '../../../store/videoSlice';
import {fetchComicInfo, pointToComic, setComicPurchase} from '../../../store/comicSlice';
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

interface UsageHandlerProps {
  open: boolean;
  onClose: () => void;
  usageType: string;
  usePoint: boolean;
  cost: number;
  description: string;
}

const UsageHandler: React.FC<UsageHandlerProps> = ({ 
  open, onClose, usageType, usePoint, cost, description
}) => {
  const {t} = useTranslation();
  const navigate = useNavigate();
  const dispatch:AppDispatch = useDispatch();
  const status = useSelector((state: RootState) => state.wallet.status?.status);
  const errorMsg = useSelector((state: RootState) => state.wallet.status?.error_msg);

  const videoInfo = useSelector((state: RootState) => state.video.videoInfoData);
  //const comicInfo = useSelector((state: RootState) => state.comic.comicInfoData);
  const episode = useSelector((state: RootState) => state.comic.comicPurchaseData?.episode) || 1;
  const comicInfo = useSelector((state: RootState) => state.comic.comicInfoData);
  const currentChapter = useSelector((state: RootState) => state.comic.currentChapter);
  const pageInfos = useSelector((state: RootState) => state.comic.pageInfoList);
  const pointToComicResult = useSelector((state: RootState) => state.comic.pointToComicResult);

  const[isRechargeModalOpen, setIsRechargeModalOpen] = useState(false);
  const[rechargeModalDes, setRechargeModalDes] = useState<ReactNode[]>([]);

  const handleConfirm = () => {
    switch (usageType) {
      case 'videoOnlineTicket':
        if (videoInfo) {
          if (usePoint) {
            if (videoInfo) {
              const uuid = videoInfo?.data.uuid;
              if (uuid) {
                dispatch(video.pointToMv({
                  num: uuid,
                  use: "1", // 同意購買
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          } else {
            if (videoInfo) {
              const uuid = videoInfo?.data.uuid;
              if (uuid) {
                dispatch(video.pointToMv({
                  num: uuid,
                  exchange: "1", // 同意購買
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          }
        }
        break;
      case 'videoDownloadTicket':
        if (videoInfo) {
          if (usePoint) {
            if (videoInfo) {
              const uuid = videoInfo?.data.uuid;
              if (uuid) {
                dispatch(video.pointToMv({
                  num: uuid,
                  dw: '1',
                  use: "1", // 同意購買
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          } else {
            if (videoInfo) {
              const uuid = videoInfo?.data.uuid;
              if (uuid) {
                dispatch(video.pointToMv({
                  num: uuid,
                  dw: '1',
                  exchange: "1", // 同意購買
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          }
        }
        break;
      case 'comicOnlineTicket':
        if (comicInfo) {
          if (usePoint) {
            if (comicInfo && currentChapter) {
              const chapter = comicInfo?.comic_chapters.filter((chapter) => chapter.ci_chapter_order === currentChapter.toString());
              const uuid = comicInfo?.comic_info.ci_uuid;
              if (chapter && uuid) {
                dispatch(pointToComic({
                  uuid: uuid,
                  chapter_uuid: chapter[0].ci_uuid,
                  use: "1", // 同意購買
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                      dispatch(fetchComicInfo(uuid));
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          } else {
            if (comicInfo && currentChapter) {
              const chapter = comicInfo?.comic_chapters.filter((chapter) => chapter.ci_chapter_order === currentChapter.toString());
              const uuid = comicInfo?.comic_info.ci_uuid;
              if (chapter && uuid) {
                dispatch(pointToComic({
                  uuid: uuid,
                  chapter_uuid: chapter[0].ci_uuid,
                  exchange: "1", // 同意購買
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                      dispatch(fetchComicInfo(uuid));
            
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          }
        }
        break;
      default:
        if (comicInfo) {
          if (usePoint) {
            if (comicInfo && currentChapter) {
              const chapter = comicInfo?.comic_chapters.filter((chapter) => chapter.ci_chapter_order === currentChapter.toString());
              const uuid = comicInfo?.comic_info.ci_uuid;
              if (chapter && uuid) {
                dispatch(pointToComic({
                  uuid: uuid,
                  chapter_uuid: chapter[0].ci_uuid,
                  dw: "1", // 同意購買
                  use: '1',
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          } else {
            if (comicInfo && currentChapter) {
              const chapter = comicInfo?.comic_chapters.filter((chapter) => chapter.ci_chapter_order === currentChapter.toString());
              const uuid = comicInfo?.comic_info.ci_uuid;
              if (chapter && uuid) {
                dispatch(pointToComic({
                  uuid: uuid,
                  chapter_uuid: chapter[0].ci_uuid,
                  dw: "1", // 同意購買
                  exchange: '1',
                  check_buy: "1"
                }))
                  .then((result) => {
                    if (result.payload.result === 'ok') {
                      showCustomToast( result.payload.result_msg, 'error');
                    } else if (result.payload.error) {
                      setRechargeModalDes([<div className='text-md p-6'>{result.payload.error_msg}</div>]);
                      setIsRechargeModalOpen(true);
                    }
                  })
                  .catch((error) => {
                    console.error('Fetch failed:', error);
                  });
              }
            }
          }
        }
        break;
    }
    onClose();
  };
  const handleRechargeModalClose = () => {
    setIsRechargeModalOpen(false);
  };

  useEffect(() => {
    if (pointToComicResult) {
      if (pointToComicResult.result === 'ok')
        showCustomToast(pointToComicResult.result_msg, 'error');
    }
  }, [pointToComicResult])

  const handleButtons = [
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={handleConfirm}>
      {t('確認')}
    </Button>,
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={onClose}>
      {t('取消')}
    </Button>
  ];
  const handleToRechargeButtons = [
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={()=>navigate('/app/recharge')}>
      {t('確認')}
    </Button>,
    <Button className="button-full active mt-1 w-1/3" type="button" onClick={handleRechargeModalClose}>
      {t('取消')}
    </Button>
  ];

  return (
    <div>
      <Modal
      content={[description]}
      open={open}
      onClose={onClose}
      buttons={handleButtons}
      />
      <Modal
        content={[rechargeModalDes]}
        open={isRechargeModalOpen}
        onClose={handleRechargeModalClose}
        buttons={handleToRechargeButtons}
      />
    </div>
  );
};

export default UsageHandler;
