import React from "react";
import { Tag } from "../../type/videoType";
import { Tag as ComicTag } from "../../type/comicType";
import { useSelector } from "react-redux";
import { RootState } from "../../store";
import { useNavigate } from "react-router-dom";
import { switchName } from "../../utils/helper";

interface TagsProps {
  tags: Tag[] | ComicTag[];
  type: string;
}

function Tags({ tags, type }: TagsProps) {
  const language = useSelector((state: RootState) => state.init.language);
  const navigate = useNavigate();

  const handle2Search = (kw: string) => {
    navigate(
      '/app/search', 
      { 
        state: { 
          kw: kw, 
          cate: type === 'video' ?  '' : 'amine'
        } 
      }
    );
  }

  return (
    <div className="flex items-center space-x-2">
      {tags.map((item, index) => (
        <span
          key={index}
          className="bg-background rounded-full text-primary-active text-xs p-2 whitespace-nowrap"
          onClick={()=>{handle2Search(item[`tag_name`] ?? '')}}
        >
          {switchName(item, language)}
        </span>
      ))}
    </div>
  );
}

export default Tags;
